/* Basic Quiz */
.paragraph--type-quiz {
  position: relative;
  font-family: "Helvetica", sans-serif; }
  .paragraph--type-quiz .field--name-ln-quiz-title {
    color: #003349;
    margin-top: 0;
    margin-bottom: 12px;
    font-size: 22px;
    line-height: 1.5;
    font-weight: 400; }
  .paragraph--type-quiz .field--name-field-quiz-title {
    margin-top: 0;
    margin-bottom: 24px;
    font-size: 22px;
    line-height: 1.5;
    font-weight: 400; }
  .paragraph--type-quiz .field--name-ln-quiz-question-number,
  .paragraph--type-quiz .field--name-ln-quiz-question-check-label,
  .paragraph--type-quiz .field--name-ln-quiz-results {
    font-size: 18px;
    line-height: 1.5;
    color: #077cab;
    font-weight: 700;
    margin-top: 24px;
    margin-bottom: 16px; }
  .paragraph--type-quiz .field--name-field-quiz-description,
  .paragraph--type-quiz .field--name-field-quiz-failed-msg,
  .paragraph--type-quiz .field--name-field-quiz-success-msg,
  .paragraph--type-quiz .field--name-field-quiz-summary {
    font-size: 18px;
    line-height: 1.5;
    font-weight: 400;
    opacity: 0.75;
    margin-bottom: 64px; }
  .paragraph--type-quiz a {
    position: relative;
    color: #fff;
    font-size: 14px;
    line-height: 1.2;
    font-weight: 400;
    text-decoration: none;
    background: #003349;
    border: none;
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    padding: 12px 24px;
    transition: all 0.3s; }
    .paragraph--type-quiz a + a {
      margin-left: 16px; }
    .paragraph--type-quiz a:hover {
      transform: translateY(-4px);
      box-shadow: 0px 4px 12px -4px rgba(0, 0, 0, 0.5);
      background: #077cab; }
  .paragraph--type-quiz .field--name-ln-quiz-start a,
  .paragraph--type-quiz .field--name-ln-quiz-next-question a {
    padding-right: 40px; }
    .paragraph--type-quiz .field--name-ln-quiz-start a:after,
    .paragraph--type-quiz .field--name-ln-quiz-next-question a:after {
      content: "";
      display: inline-block;
      position: absolute;
      top: 50%;
      right: 24px;
      width: 7px;
      height: 7px;
      border-right: 2px solid #fff;
      border-bottom: 2px solid #fff;
      vertical-align: bottom;
      transform: translateY(-50%) rotate(-45deg); }
